/* New games style */

nav.gameinput {
    height: 75%;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    align-content: flex-start;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
}

nav.gameinput p {
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 16px;
    color: white;
    width: 100%;
    margin-left: 50px;
    font-weight: bold;
    margin-top: 25px;
    margin-bottom: 25px;
}

nav.gameinput form {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
}

nav.gameinput form div {
    height: 30%;
    width: 65%;
    background-color: #ffffff;
    border-radius: 5px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
}

input.imageinput {
    text-decoration: none; 
    color: black;
}

nav.gameinput form label  {
    font-weight: bold;
    font-size: 15px;
    font-family: 'Nunito';
    width: 70%;
    color: #ffffff;
    margin-top: 25px;
    margin-bottom: 25px ;
    text-align: justify;
}

input.textinput  {
    color: #ffffff;
    background-color:#444444;
    height: 5%;
    width: 75%;
}

input.bigtextinput  {
    color: #ffffff;
    background-color:#444444;
    height: 15%;
    width: 75%;
}

nav.gameinput form button  {
    width: 85%;
    height: 10%;
    margin-left: 7.5%;
    margin-top: 5%;
    border: none;
    cursor: pointer;
    box-shadow: 0.5rem 0.5rem 0.5rem #0a0a0a;
    font-family: 'Press Start 2P';
    background-image: linear-gradient(to right, rgb(51, 168, 5), #86d33a);
    transition: 400ms;
}

nav.gameinput form button:hover{
    color: #ffffff;
    background-image: linear-gradient(to right, #86d33a, #5b5f58);
}