/* Posts styles */

div.post {
    position: relative;
    height: 575px; 
    width: 500px;
    background-color: #232222; 
    border: solid 2px #302e2e ;
    border-radius: 5px;
    box-shadow: 0.5rem 0.5rem 0.5rem #0a0a0a;
    margin-top: 25px;
    transition: 1000ms;
    display: flex;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
}

@media screen and (max-width: 1750px) {
    div.post {
        height: 479px; 
        width: 416px;
    }
}

@media screen and (max-width: 1450px) {
    div.post {
        height: 383px; 
        width: 333px;
    }
}

@media screen and (max-width: 1100px) {
    div.post {
        height: 328px; 
        width: 285px;
    }
}

@media screen and (max-width: 500px) {
    div.post {
        height: 287px; 
        width: 250px;
    }
}

div.post nav {
    height: 15%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
}

div.post nav img {
    height: 60%;
    width: 12%;
    margin-left: 2%;
    margin-right: 2%;
}

div.post nav p {
    font-size: 20px;
    color: #ffffff;
    font-weight: bolder;
    font-family: 'nunito';
}

@media screen and (max-width: 1100px) {
    div.post nav p {
        font-size: 12px;
    }
}

img.imagepost {
    height: 70%;
    width: 98%;
}

div.post div {
    height: 15%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
}

div.post div img {
    height: 55%;
    width: 10%;
    margin-left: 5%;
    margin-right: 2%;
}

div.post div p {
    font-family: 'Press Start 2P';
    font-size: 15px;
}

@media screen and (max-width: 1100px) {
    div.post div p {
        font-size: 8px;
    }
}

div.post div nav {
    width: 55%;
    height: 100%;
    margin-left: 10%;
    display: flex;
}

div.post div nav p {
    font-family: 'font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif';
    font-weight: lighter;
}

.like-btn {
    background-color: #232222;
    border: none;
}